import api from "@/common/index.js";
import over from '@/components/over.vue';

let mvc = {
	dateStart: '',
	dateEnd: '',
	filterIndex: 0,
	priceOrder: 0, //1 价格从低到高 2价格从高到低
	downNum: '',
	list: [],
	over: false
}
export default {
	components: {
		over
	},
	data() {
		return {
			mvc: {
				dateStart: '',
				dateEnd: '',
				filterIndex: 0,
				priceOrder: 0, //1 价格从低到高 2价格从高到低
				downNum: '',
				list: [],
				over: false
			}

		}
	},
	computed: {
		i18n() {
			return this.$t('market_data');
		}
	},
	onLoad(option) {
		let that = this;
		setTimeout(function() {
			api.load(that.$t('index').loadmsg)
			api.pageTitle(that.$t('market_data').title);
			mvc.dateStart = option.time1;
			mvc.dateEnd = option.time2;
			that.loadData();
		}, 300)
	},
	methods: {
		tabClick(index) {
			if (index == 0) {
				mvc.filterIndex = index;
				mvc.downNum = "";
				this.loadData();
				this.mvc = mvc
				return false;
			}
			mvc.filterIndex = index;
			if (index === 2) {
				mvc.priceOrder = mvc.priceOrder === 1 ? 2 : 1;

				if (mvc.priceOrder == 1) {
					mvc.downNum = "desc"
				};
				if (mvc.priceOrder == 2) {
					mvc.downNum = "asc"
				};
				uni.pageScrollTo({
					duration: 300,
					scrollTop: 0
				})
				this.loadData();
			}
			this.mvc = mvc
		},
		loadData() {
			api.load(this.$t('index').loadmsg)

			let that = this;
			let params = ''
			if (mvc.downNum == '') {

				params = {
					sdate: mvc.dateStart,
					edate: mvc.dateEnd
				}

			} else {
				if (mvc.downNum == 'asc') {

					params = {
						sdate: mvc.dateStart,
						edate: mvc.dateEnd,
						type: "asc"
					}
				};
				if (mvc.downNum == 'desc') {

					params = {
						sdate: mvc.dateStart,
						edate: mvc.dateEnd,
						type: "desc"
					}
				};
			}
			that.$ajax.marketView(params).then(res => {
				let list = res;
				let weekDay = that.$t('market_data').weekDay;
				if (list.length != 0) {
					list.forEach(function(item, index) {
						let num = item.AMT0 - item.IAMT;
						let myDate = new Date(Date.parse(item.TIME.replace(/-/g, "/")));
						item.price1 = num.toFixed(2);
						item.day = weekDay[myDate.getDay()]
					})
					mvc.list = res;
					mvc.over = true;

				} else {
					mvc.hide = true;
				}
				uni.hideLoading();
				that.mvc = mvc;
			})

		}
	}
}
